export default "/* Add application styles & imports to this file! */\n*,\n*:before,\n*:after {\n  box-sizing: inherit;\n}\nbody {\n  font-family: \"Antic Slab\", serif;\n}\nhr {\n  width: 100%;\n}\n/*\ntoggle styles copied and modified from\nhttps://codepen.io/mallendeo/pen/eLIiG\nby Mauricio Allende (https://mallendeo.com/)\n */\n.toggle-btn {\n  display: inline-block;\n  outline: 0;\n  width: 3em;\n  height: 1.5rem;\n  position: relative;\n  cursor: pointer;\n  -webkit-user-select: none;\n     -moz-user-select: none;\n      -ms-user-select: none;\n          user-select: none;\n  background: #fbfbfb;\n  border-radius: 2em;\n  padding: 2px;\n  transition: all 0.4s ease;\n  border: 1px solid #e8eae9;\n}\n.toggle-btn:focus::after,\n.toggle-btn:active::after {\n  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 4px 0 rgba(0, 0, 0, 0.08), inset 0px 0px 0px 1.5px #9c9c9c;\n}\n.toggle-btn::after {\n  left: 0;\n  position: relative;\n  display: block;\n  content: \"\";\n  width: 50%;\n  height: 100%;\n  border-radius: 2em;\n  background: #fbfbfb;\n  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275), padding 0.3s ease, margin 0.3s ease;\n  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 4px 0 rgba(0, 0, 0, 0.08);\n}\n.toggle-btn.toggle-btn-on::after {\n  left: 50%;\n}\n.toggle-btn.toggle-btn-on {\n  background: #86d993;\n}\n.toggle-btn.toggle-btn-on:active {\n  box-shadow: none;\n}\n.toggle-btn.toggle-btn-on:active::after {\n  margin-left: -0.8em;\n}\n.toggle-btn:active::after {\n  padding-right: 0.8em;\n}\n.toggle-input {\n  display: none;\n}\n/*# sourceMappingURL=data:application/json;base64, */";